.footer-bg {
  position: relative;
  background-color: var(--very-dk-cyan);
}
.footer-bg::before {
  content: "";
  background-image: url("../images/bg-footer-top-mobile.svg");
  background-repeat: no-repeat;
  background-position: top;
  background-size: 100% 100%;
  position: absolute;
  top: -8%;
  right: 0;
  bottom: 100%;
  left: 0;
}
@media (min-width: 40em) {
  .footer-bg::before {
    background-image: url("../images/bg-footer-top-desktop.svg");
    top: -18%;
  }
}
@media (min-width: 71.875em) {
  .footer-bg::before {
    background-image: url("../images/bg-footer-top-desktop.svg");
    top: -40%;
  }
}

.footer {
  padding-top: 5rem;
  padding-bottom: 3.125rem;
  gap: 5rem;
  color: var(--white);
  font-weight: 600;
  text-align: left;
}
.footer__logo {
  display: inline-block;
}
.footer__header {
  font-family: var(--font-heading);
  font-size: 1.25rem;
  text-transform: uppercase;
}
.footer__content {
  margin-bottom: 1.875rem;
  line-height: 1.8;
}
.footer__email {
  width: 100%;
  margin-bottom: 0.625rem;
  border-radius: 0.3125rem;
  border: none;
  padding: 0.9375rem;
  font-size: 1rem;
}
.footer__submit {
  width: 50%;
  float: right;
  color: var(--white);
  background-color: var(--pink);
  border-radius: 0.3125rem;
  border: none;
  padding: 0.9375rem;
  font-size: 1rem;
  font-weight: 400;
  transition: background-color 300ms ease-in-out;
}
.footer__submit:hover {
  cursor: pointer;
  background-color: var(--lt-pink);
}
.footer-clearfix {
  content: "";
  clear: both;
  display: table;
}
@media (min-width: 40em) {
  .footer {
    flex-direction: row;
    gap: 12.5rem;
  }
  .footer-item1 {
    order: 2;
  }
  .footer-item2 {
    order: 1;
  }
}
.footer__phonenum {
  display: block;
  margin-bottom: 0.625rem;
}
.footer__phonenum::before {
  content: url("../images/icon-phone.svg");
  margin-right: 1.25rem;
}
.footer__emailadd {
  display: block;
  margin-bottom: 1.875rem;
}
.footer__emailadd::before {
  content: url("../images/icon-email.svg");
  margin-right: 1.25rem;
}
.footer__links {
  display: inline-block;
  font-size: 1.875rem;
  color: var(--white);
}
.footer__links:not(:last-child) {
  margin-right: 0.9375rem;
}